@import "https://use.fontawesome.com/releases/v5.6.3/css/all.css";
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

li {
  list-style:none;
  display:block;
}

.navigation{
  background-color:#fff;
  height:41px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  position: relative;
  padding:6px 12px;
  text-align: center;
  z-index: 10;
}

.navigation .title {
  font-weight:400;
  font-size:14px;
  line-height:43px;
}

.navigation .float_dashboard{
  position: fixed;
  top:-200%;
  left:0px;
  width:100%;
  transition: 0.5s;
  padding-bottom: 40px;
  padding-left:20vw;
}

.navigation .float_dashboard.active{
  top:53px;
  background:white;
}

.navigation .float_dashboard > li {
  font-size:16px;
  font-weight:100;
  text-align: left;
  margin-top:40px;
  opacity: 0.3;
  transition:0.5s;
}

.navigation .float_dashboard > li.active,
.navigation .float_dashboard > li:hover {
  text-decoration: underline;
  opacity: 1;
}
.navigation .float_dashboard > li.active > i,
.navigation .float_dashboard > li:hover > i {
  color:red;
}

.navigation .float_btn {
  background: #fff;
  position: absolute;
  right: 12px;
  top: 12px;
  width: 50px;
  height: 32px;
  border-radius: 5px;
  border: 1px solid lightgray;
}

.navigation .menuIcons{
}

.navigation .menuIcons i {
  color:#ddd;
  font-size:40px;
  width:40px;
  font-weight:bold;
  text-align:center;
  margin-right:40px;
}

.header{
  width:100%;
  background: url(img/bg-pattern.png),linear-gradient(to left,#7b4397,#dc2430);
  color:white;
  padding-top:120px;
  padding-bottom:100px;
}

.header h5 {
  font-family: Catamaran,Helvetica,Arial,sans-serif;
  font-weight: 200;
  letter-spacing: 1px;
  font-size:30px;
  width:80%;
  margin:0 auto;
  text-align: center;
}